body {
  width: 100%;
  background-color: #f5f5f5;
  justify-content: flex-start;
  align-items: flex-start;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  display: flex;
}

.container {
  width: 100%;
  min-height: 100vh;
  text-align: left;
  background-color: #fff;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

.loader {
  width: 50px;
  height: 50px;
  border: 8px solid #f3f3f3;
  border-top-color: #3498db;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  display: none;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error {
  color: red;
  margin-bottom: 20px;
  display: none;
}

.breed-select {
  max-width: 300px;
  width: 100%;
  margin-bottom: 20px;
}

.cat-info {
  max-width: 300px;
  margin-top: 20px;
}

#catImage {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
  margin-bottom: 10px;
}

#catDetails {
  text-align: left;
}

